//========== VARIABLES ==========
:root {
  /** COLORS **/
  --dark-blue: hsl(240, 38%, 20%);
  --grayish-blue: hsl(0, 0%, 94%);
  --dark-grayish-blue: #d8d8d8;
  --app-bar: #1f2e78;
  --border-default: gray;
  --dark-red: #e02020;
  --yellow: #f7b500;
  --green: #6dd400;
  --font: #222428;
}
//========== RESETS ==========
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
//========== TYPOGRAPHY ==========
html {
  // font-size: 67.5%; // sets 1 rem = 10px based on browser default
}

html,
body,
main,
div,
span,
ul,
a,
p,
section,
article {
  font-family: Arial, Helvetica, sans-serif;
  color: var(--font);
  margin: 0;
  text-decoration: none;
  list-style-type: none;
}
li {
  cursor: pointer;
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--grayish-blue);
}

svg {
  width: 100px;
  height: 100px;
}
